    vertical-align: top;
    margin: 0 15px 15px 0;
    width: 216px;
    text-align: left;
    background-color: #fff;
    position: relative;
    border: none;
    box-shadow: none
}

.card:before,.card:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    transition: .2s ease;
    pointer-events: none
}

.card:before {
    box-shadow: 0 0 1px 1px rgba(28,29,31,.1),0 3px 1px 0 rgba(28,29,31,.1);
    opacity: 1
}

.card:after {
    opacity: 0;
    box-shadow: 0 2px 8px 2px rgba(28,29,31,.15)
}

.card:hover:before {
    opacity: 0
}

.card:hover:after {
    opacity: 1
}

.card a {
    color: #1c1d1f;
    display: block;
    font-size: 13px
}

.card a:hover img {
    opacity: .5
}

.card a:hover .card__instructors,.card a:hover .card__content-info {
    opacity: 1
}

.card a:hover .card__instructors img,.card a:hover .card__content-info img {
    opacity: 1
}

.card a:hover:after {
    opacity: 1
}

.card .details__name,.card .details__instructor {
    height: 39px;
    display: block!important;
    display: -webkit-box!important;
    -webkit-line-clamp: 2;
    -moz-line-clamp: 2;
    -ms-line-clamp: 2;
    -o-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
    -moz-box-orient: vertical;
    -ms-box-orient: vertical;
    -o-box-orient: vertical;
    box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal
}

.card .details__rating {
    margin-bottom: 5px
}

.card .review-stars {
    font-size: 17px
}

.card .review-stars label {
    cursor: pointer
}

.card .star-rating--static {
    margin-right: 5px
}

.card .options__wishlist .tooltip {
    top: 0;
    right: 20px;
    visibility: hidden;
    transition: .25s cubic-bezier(.46,.03,.52,.96)
}

.card .options__wishlist .tooltip__arrow {
    border-left-color: #fff
}

.card .options__wishlist .tooltip__inner {
    max-width: 175px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: right;
    background-color: #fff;
    color: #3e4143
}

.card .options__wishlist .icon {
    font-size: 18px;
    color: #3e4143;
    text-stroke: 1px #fff;
    -webkit-text-stroke: 1px #fff
}

.ie .card .options__wishlist .icon {
    text-shadow: -1px -1px 0 #fff,1px -1px 0 #fff,-1px 1px 0 #fff,1px 1px 0 #fff
}

.card .options__wishlist .icon::before {
    float: right
}

.card .options__wishlist.active .icon {
    color: #e25d3f;
    text-stroke: 1px #fff;
    -webkit-text-stroke: 1px #fff
}

.ie .card .options__wishlist.active .icon {
    text-shadow: -1px -1px 0 #fff,1px -1px 0 #fff,-1px 1px 0 #fff,1px 1px 0 #fff
}

.card .options__wishlist:hover {
    cursor: pointer
}

.card .options__wishlist:hover .tooltip {
    opacity: 1;
    visibility: visible
}

.card .options__wishlist:hover .icon {
